document.querySelectorAll(".blog-actual .tabs__nav-item").forEach(elem => {
  elem.addEventListener("click", function () {
    document.querySelectorAll(".blog-actual .blog-post").forEach(elem2 => {
      elem2.classList.add("Opacity")
    })
  })
})

document.querySelectorAll(".nav .tabs__nav-item").forEach((elem, i) => {
  elem.addEventListener("click", function () {
    let indexMenu = i;
    localStorage.setItem('indexMenu', indexMenu);


    document.querySelectorAll(".nav .header__card").forEach(elem2 => {
      elem2.classList.add("Opacity")
    })

  });
})

setTimeout(() => {
  localStorage.clear();
}, 1000);

document.querySelectorAll(".products-solution .tabs__nav li").forEach((elem, i) => {
  if (i == Number(localStorage.indexMenu)) {
    elem.querySelector("button").click()


    elem.click()

    document.querySelectorAll(".products-solution .enterprise__cards").forEach(elem => {
      elem.classList.add("Click")
    })
  }
})
