document.querySelector('#uploaded_file').addEventListener('change', file_selected);

function file_selected() {
  try {
    var file = document.getElementById('uploaded_file').files[0];
    if (file) {
      var file_size = 0;
      if (file.size > 1024 * 1024) {
        file_size = (Math.round(file.size * 100 / (1024 * 1024)) / 100).toString() + 'MB';
      }
      else {
        file_size = (Math.round(file.size * 100 / 1024) / 100).toString() + 'KB';
      }
      document.getElementById('file_name').innerHTML = 'Название файла: ' + file.name;
      document.getElementById('file_size').innerHTML = 'Size: ' + file_size;
    }
  }
  catch (e) {
    var file = document.getElementById('uploaded_file').value;
    file = file.replace(/\\/g, "/").split('/').pop();
    document.getElementById('file_name').innerHTML = 'Название файла: ' + file;
  }
}
